import React from 'react';
import DocumentSizesIllustration from './DocumentSizesIllustration';

import {
    SectionContainer,
    SectionTitle,
    SectionInfo,
    SectionText,
    SectionDownloadButton
} from '../styled/Section';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilePdf } from '@fortawesome/free-solid-svg-icons';


class Adds extends React.Component {

	render() {
		return (
			<SectionContainer>
				<SectionTitle>Specyfikacja reklam</SectionTitle>

				<SectionText>
				Podstawowe dane techniczne dla plików graficznych:
					<ul>
						<li>przestrzeń kolorystyczna: RGB</li>
						<li>format pliku: PNG-24</li>
						<li>preferowana rozdzielczość: 220 dpi</li>
					</ul>
				</SectionText>


				<SectionInfo>
					Wymiary reklamy całostronicowej
				</SectionInfo>

				<DocumentSizesIllustration
					width="1240"
					height="1754"
					dpi="150"
				/>

				<DocumentSizesIllustration
					width="1818"
					height="2572"
					dpi="220"
				/>

				<SectionText>
				W przypadku reklamy zajmującej część strony należy zachować 
				rozmiar wysokości lub szerokości reklamy całostronicowej.
				</SectionText> 


                <SectionText>
					    <SectionDownloadButton
            	    	 	href="download/Szortal na Wynos - specyfikacja reklam - 2019.pdf"
            	    	 	download="Szortal na Wynos - specyfikacja reklam - 2019.pdf"
            	    	>
            	    		<FontAwesomeIcon
                                    icon={ faFilePdf }
                                    size="2x"
                                    color="#d22"
                              />
            	    	</SectionDownloadButton>
					Kliknij, aby ściągnąć specyfikację reklam w formacie PDF.
                </SectionText>
				
			
			</SectionContainer>
		)
	}
}

export default Adds;


